  import React, { useState } from 'react'
  import { Document, Page, pdfjs} from 'react-pdf';
  import styled from 'styled-components'

  import { SizeMe } from 'react-sizeme'

  import GlobalWrapper from 'components/global-wrapper'
  import media from 'utils/media-queries'
  import Link from 'gatsby-link'


  import Header from 'components/article/header'
  import CoverImage from 'components/article/coverimage'
  import ContentWrapper from 'components/article/contentwrapper'
  import Footer from 'components/footer'
  import FullSizeImage from 'components/article/fullsizeimage'
  import LargeImage from 'components/article/largeimage'

  import CoverSrc from 'img/articles/comp/blacktaj/00_black_taj_intro.png'
  import ContentPDF from 'img/articles/comp/blacktaj/01_black_taj.pdf'

  pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js`;

  const Section = styled.section`
    margin: 64px 0;
    ${media.sm`
      margin: 0;
    `}
  `

  const Content = styled.div`
    canvas {
      width: 100% !important;
      height: auto !important;
    }
    max-width: 100%;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    p + h3 {
      margin-top: 42px;
    }
    padding: 0 0 0 0;
    ${media.lg`
      padding-right: 0;
    `}
    ${media.xs`
    max-width: 700px;
    padding: 0 0 0 0;
    `}
  `

  const EmbedContainer = styled.div`
    position: relative;
    padding-bottom: 56.25%;
    height: 0;
    overflow: hidden;
    width: 100%;
    height: auto;
    margin-top: 32px;
    margin-bottom: 32px;
    @media (min-width: 1000px) {
      width: 1000px;
      height: 562px;
      padding-bottom: 0px;
      margin-left: auto;
      margin-right: auto;
    }
  `

  const Video = styled.iframe`
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  `

  const ImageGrid = styled.div`
    background: #f2f2f2;
    max-width: 100%;
    padding: 40px;
    margin: 32px 0;
    text-align: center;
    ${media.lg`
      padding: 32px;
      margin: 0 0 24px 0;
    `}
    ${media.md`
      margin: 0;
      margin: 0 0 16px 0;
    `}
    ${media.sm`
      padding: 16px;
    `}
  `

  const Img = styled.img`
    max-width: calc(100% - 96px);
    max-height: 300px;
    margin: 8px;
    display: inline-block;
    ${media.xl`
      max-height: 240px;
    `}
    ${media.lg`
      max-height: 200px;
    `}
    ${media.md`
      max-height: 120px;
      margin: 4px;
    `}
    ${media.sm`
      max-height: 80px;
      margin: 2px;
    `}
  `

  const LifeTime = () => {
    const [numPages, setNumPages] = useState(null);
    return (
      <GlobalWrapper>
        <Header title="The Black Taj" lead="Aux-siah bagh’s garden of paradises" />
        <CoverImage src={CoverSrc} focusX={'20%'} focusY={'100%'} />
        <Section>
          <ContentWrapper>
            <h2>The Black Taj</h2>
            <p><b>Team //</b> Ekta Rakholiya (India), Franklin Gaspar (Brazil), Norman Wei (New Zealand), Udit Goyal (India).
</p>
            <p><i>Finalist, out of 400 proposals from 35 different countries</i></p>
            <p>
            The Black Taj Mahal (Persian/Urdu: لحم جات ہایس “Black Taj”, also “the 2nd Taj”) is a legendary 
            black marble mausoleum that was planned to be built across the Yamuna River directly opposite 
            the Taj Mahal.It is an incomplete memory of Mughal emperor Shah Jahan. In 2009, when the pool of 
            Mehtab Bagh - a garden complex that lies north of the Taj across the river - was re-created for 
            historical studies it showed the Black Taj in reflection clearly. Does this imply that Shah 
            Jahan already pinned the location himself when he was planning the original Taj? 
            </p>
            <p>
            Our proposal envisions this incomplete memory with Aux-siah bagh - garden of paradise. Instead of overpowering Taj Mehal,
            it curates Black Taj on reflective surfaces. 
            </p>
          </ContentWrapper>
        </Section>
        <Content>
        <SizeMe
          monitorHeight
          refreshRate={128}
          refreshMode={"debounce"}
          render={({ size }) => (
          <div>
            <Document 
              file={ContentPDF} 
              onLoadSuccess={({ numPages })=>setNumPages(numPages)}>
              {Array.apply(null, Array(numPages))
                  .map((x, i)=>i+1)
                  .map(page => <Page pageNumber={page} width={size.width} renderMode="png" scale={1.0}/>)}
            </Document>
          </div>
        )} />
        </Content>
        <Footer />
      </GlobalWrapper>
    )
  }

  export default LifeTime
